.group {
  display: flex;
}
.group--horizontal {
  flex-direction: row;
}
.group--vertical {
  flex-direction: column;
}
.group--grow {
  width: 100%;
  height: 100%;
}
.group--wrap {
  flex-wrap: wrap;
}
.group--justify-space-between {
  justify-content: space-between;
}
.group--justify-end {
  justify-content: flex-end;
}
.group--justify-center {
  justify-content: center;
}
.group--align-stretch {
  align-items: stretch;
}
